/**
 * Navigation component with 2 links buttons to:
 * @Version_I (http://localhost:3000/home)
 * @Version_II (http://localhost:3000/version-ii)
 * 
 */
//------------------------------------------------------------------------------

// libraries 
import { Disclosure } from '@headlessui/react'
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline'
import { Link } from "react-router-dom";
//------------------------------------------------------------------------------

interface NavbarProps {
  /**
   * Empty props
   */
}

// navigation props
const navigation = [
  { name: 'Version I', href: '/', current: true },
  { name: 'Version II', href: '/version-ii', current: false },
]

// help function
function classNames(...classes: any ) {
  return classes.filter(Boolean).join(' ')
}

// Component
export function Navbar({ }: NavbarProps) {
  return (
    <header className="sticky top-0 bg-white  z-50 shadow-xl shadow-blue-600/20" >
      <Disclosure as="nav" className="bg-slate-50 ">
        {({ open }) => (
          <>
            <div className="mx-auto max-w-7xl px-2 sm:px-6 lg:px-8">
              <div className="relative flex h-16 items-center justify-between">
                <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
                  {/* Mobile menu button*/}
                  <Disclosure.Button className="inline-flex items-center justify-center rounded-md p-2 text-gray-800 hover:bg-gray-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
                    <span className="sr-only">Open main menu</span>
                    {open ? (
                      <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                    ) : (
                      <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                    )}
                  </Disclosure.Button>
                </div>
                <div className="flex flex-1 items-center justify-center sm:items-stretch sm:justify-between">
                  <div  className="flex flex-shrink-0 items-center cursor-pointer">
                    {/* <img
                      className="block h-10 w-auto lg:hidden"
                      src="/images/logo.svg" 
                      alt="Logo"
                    />
                    <img
                      className="hidden h-10 w-auto lg:block"
                      src="/images/logo.svg"
                      alt="Logo"
                    /> */}
                  </div>
                  <div className="hidden sm:ml-6 sm:block">
                    <div className="flex space-x-4">
                      {navigation.map((item) => (
                        <Link
                          key={item.name}
                          to={item.href}
                          className={classNames(
                            'text-gray-500 hover:bg-indigo-500 hover:text-white',
                            'px-3 py-2 rounded-md text-sm font-medium'
                          )}
                          aria-current={item.current ? 'page' : undefined}
                        >
                          {item.name}
                        </Link>
                      ))}
                    </div>
                  </div>
                  <div className="hidden sm:ml-6 sm:block">
                  </div>
                </div>
              </div>
            </div>

            <Disclosure.Panel className="sm:hidden">
              <div className="space-y-1 px-2 pt-2 pb-3">
                {navigation.map((item) => (
                  <Disclosure.Button
                    key={item.name}
                    as="a"
                    href={item.href}
                    className={classNames(
                      'text-gray-500 hover:bg-indigo-500 hover:text-white',
                      'block px-3 py-2 rounded-md text-base font-medium'
                    )}
                    aria-current={item.current ? 'page' : undefined}
                  >
                    {item.name}
                  </Disclosure.Button>
                ))}
              </div>
            </Disclosure.Panel>
          </>
        )}
      </Disclosure>
    </header>
  );
}
